import React, { memo, useState, useEffect } from 'react';
import cloneDeep from 'lodash/cloneDeep';
import "@/style/@pages/takoyaki.scss";
import basic from '@/assets/images/basic.png';
import o from '@/assets/images/0.png';
import c1 from '@/assets/images/c1.png';
import c3 from '@/assets/images/c3.png';
import c5 from '@/assets/images/c5.png';
import c6 from '@/assets/images/c6.png';
import c14 from '@/assets/images/c14.png';
import c17 from '@/assets/images/c17.png';
import c20 from '@/assets/images/c20.png';
import c21 from '@/assets/images/c21.png';
import c27 from '@/assets/images/c27.png';
import c30 from '@/assets/images/c30.png';
import c31 from '@/assets/images/c31.png';
import c32 from '@/assets/images/c32.png';
import c33 from '@/assets/images/c33.png';
import c34 from '@/assets/images/c34.png';
import c36 from '@/assets/images/c36.png';

const TakoyakiList = [
  c1, c3, c5, c6, c14, c17, c20, c21, c27, c30, c31, c32, c33, c34, c36
];

const defaultTakoyakiList = [
  { id: 1, img: o },
  { id: 2, img: o },
  { id: 3, img: o },
  { id: 4, img: o },
  { id: 5, img: o },
  { id: 6, img: o },
  { id: 7, img: o },
  { id: 8, img: o },
  { id: 9, img: o },
  { id: 10, img: o },
  { id: 11, img: o },
  { id: 12, img: o },
  { id: 13, img: o },
  { id: 14, img: o },
  { id: 15, img: o },
  { id: 16, img: o },
  { id: 17, img: o },
  { id: 18, img: o },
  { id: 19, img: o },
  { id: 20, img: o },
  { id: 21, img: o },
  { id: 22, img: o },
  { id: 23, img: o },
  { id: 24, img: o },
  { id: 25, img: o },
  { id: 26, img: o },
  { id: 27, img: o },
  { id: 28, img: o },
  { id: 29, img: o },
  { id: 30, img: o }
];

export const TakoyakiMain = ({ setSelect, select, takoyaki }) => {
  return (
    <div className='takoyaki-main' onClick={()=>setSelect(false)}>
      {takoyaki.map((item, index) => (
        <button
          className={`item ${select === index ? 'selected-item' : ''}`} // 使用模板字符串动态添加 selected-item 类
          key={index}
          onClick={(e) => {
            e.stopPropagation();
            setSelect(index);
          }}
        >
          <img src={item.img} alt={`item ${index}`}></img>
        </button>
      ))}
    </div>
  );
}

const findDuplicateImages = (takoyaki) => {
  const imageCount = {};
  takoyaki.forEach(item => {
    imageCount[item.img] = (imageCount[item.img] || 0) + 1;
  });
  return new Set(Object.keys(imageCount).filter(key => imageCount[key] > 1));
};

export const TakoyakiController = ({ setSelect, takoyaki, select }) => {
  const duplicateImages = findDuplicateImages(takoyaki);

  const processedList = TakoyakiList
    .map(item => ({ img: item, isDuplicate: duplicateImages.has(item) }))
    .sort((a, b) => a.isDuplicate === b.isDuplicate ? 0 : a.isDuplicate ? 1 : -1);

  const setNewTakoyaki = (item) => {
    takoyaki[select].img = item;
    setSelect(false);
  };

  return (
    <div 
      className='takoyaki-controller' 
      style={select !== false ? {transform: 'translate(0%, -50%)'} : {transform: 'translate(-100%, -50%)'}}
    >
      {processedList.map((item, index) => (
        <button
          className={`item ${item.isDuplicate ? 'duplicate' : ''}`}
          key={index}
          onClick={() => setNewTakoyaki(item.img)}
          style={item.isDuplicate ? { opacity: 0.5 } : {}}
        >
          <img src={item.img} alt='item'></img>
        </button>
      ))}
    </div>
  );
};

const Takoyaki = () => {
  const [takoyaki, setTakoyaki] = useState([]);
  const [select, setSelect] = useState(false);

  const setNewTakoyaki = (item) => {
    const newTakoyaki = cloneDeep(defaultTakoyakiList);
    setTakoyaki(newTakoyaki);
    setSelect(false);
  }

  useEffect(() => {
    setNewTakoyaki();
  }, []);

  return (
    <div className='takoyaki'>
      <div className='takoyaki-container'>
        <img src={basic} alt='basic' className='basic-img' onClick={()=>setSelect(false)} />
        <TakoyakiMain setSelect={setSelect} select={select} takoyaki={takoyaki}/>
        <TakoyakiController setSelect={setSelect} select={select} takoyaki={takoyaki} />
        <button className='reset-btn' onClick={setNewTakoyaki}>重置</button>
      </div>
    </div>
  );
};

export default memo(Takoyaki);
