import React, { createContext, useReducer } from 'react';

// 建立共用環境
const LiffContext = createContext(null);

// initialState
const initialState = {
    me: null,
};

// reducer
const reducer = (state, action) => {
    switch (action.type) {
        case 'SET_ME':
            return {
                ...state,
                me: action.payload,
            };
        default:
            throw Error('Unknown action: ' + action.type);
    }
};

// 建立provider元件
const LiffProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, initialState);

    const actions = {
        setMe: (payload) => {
            dispatch({
                type: 'SET_ME',
                payload,
            });
        },
    };

    return (
        <LiffContext.Provider
            value={{
                liffState: state,
                liffActions: actions,
            }}>
            {children}
        </LiffContext.Provider>
    );
};

export { LiffContext, LiffProvider };
